<template>
	<div>
		<div class="login-title">
				<h3>账号登录</h3>
				<p>没有账号？
					<router-link class="lt-register" tag="span" to="register">立即注册</router-link>
				</p>
		</div>
		<div class="lr-content">
			<el-form :model='user'>
				<el-form-item>
					<el-input v-model="user.userName" placeholder="账号"/>
				</el-form-item>
				<el-form-item>
					<el-input type="password" v-model="user.userPassword" placeholder="密码" />
				</el-form-item>
				<el-form-item>
					<el-button class="lr_btn" @click.native='login'>立即登录</el-button>
				</el-form-item>
			</el-form>
		</div>
	</div>
</template>

<script>
	import {userLogin, getUserInfo} from "@/network/user";
	export default {
		name: 'login',
		data() {
			return {
				user: {
					userName: '',
					userPassword: ''
				}
			}
		},
		methods: {
			login() {
				userLogin(this.user.userName, this.user.userPassword).then(
					res => {
						this.$message({
							showClose: true,
							message: res.msg,
							duration: 1000
						});
						if (res.code === 1) {
							getUserInfo().then(res => {
								if(res) {
									this.$store.commit('user/updateUserInfo', res.data)
									this.$router.push('/home')
								}
							})
						}
				});
			}
		}
	}
</script>

<style>
	.lt-register{
		color: #008489 !important;
		cursor: pointer;
	}
	.lt-register:hover {
		padding-bottom: 1px;
		border-bottom: 1px solid #008489;
	}
</style>
